import React from "react"
import { Image } from "grommet"
import ProgressiveImage from "react-progressive-image"

export const FALLBACK_IMAGE =
  "https://parsefiles.back4app.com/32hrI9O4UWscR1vOtkNt62eoLtZgyS9SUIEwp0HL/ef090da5003a00bc6c4bf12255c43aa3_fallback.png"

const Component = ({ src, onImageClick, imageProps, ...props }) => {
  const placeholder = <Image src={FALLBACK_IMAGE} fit="cover" />

  return (
    <>
      <ProgressiveImage src={src} placeholder={FALLBACK_IMAGE}>
        {(src, loading) => {
          return loading ? (
            placeholder
          ) : (
            <>
              <Image
                fallback={FALLBACK_IMAGE}
                src={src}
                onClick={onImageClick}
                {...imageProps}
              />
            </>
          )
        }}
      </ProgressiveImage>
    </>
  )
}

export default Component
