import React, { useState } from "react"
import { Box, ResponsiveContext } from "grommet"

import { navigate } from "gatsby"
import Modal from "../../components/Modal"
const Component = ({
  dataList,
  onClickAppsIcon,
  showFallBack = false,
  isOwner,
  vertical,
  render,
}) => {
  const avatarSrc = ""
  const size = React.useContext(ResponsiveContext)
  const [showModal, setShowModal] = useState(false)
  const [activeCardImage, setActiveCardImage] = useState([])

  const widthLookup = {
    xxsmall: "100%",
    xsmall: "100%",
    small: "100%",
    medium: "24%",
    xlarge: "20%",
    full: "20%",
  }
  return (
    <Box
      fill={true}
      wrap={true}
      direction={vertical ? "column" : "row"}
    >
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, cta, cardHref, image, description } = e
          const boxProps = cardHref
            ? {
                style: { cursor: "pointer" },
                onClick: () => navigate(cardHref),
              }
            : {}
          return (
            <Box
              {...boxProps}
              outline={false}
              key={i}
              width={widthLookup[size]}
              gap="xxsmall"
              direction={vertical ? "row" : "column"}
              pad="5px"
            >
              {render && render(e, i)}
            </Box>
          )
        })}
      {showModal && activeCardImage && (
        <Modal onClose={() => setShowModal(false)}>
          <Box></Box>
        </Modal>
      )}
    </Box>
  )
}

export default Component
